.pagination {
	padding-top: 3rem;
	padding-bottom: 6rem;
}

.pagination__list {
	display: flex;
	align-items: center;
	justify-content: center;
	padding-left: 0;
	list-style-type: none;
}
.pagination__item {
	margin: 0 5px;
}
.pagination__link {
	width: 3.5rem;
	height: 3.5rem;
	border-radius: 5px;
	display: flex;
	justify-content: center;
	align-items: center;
	color: var(--ink-50);
	border: 1px solid var(--black);
	&:hover {
		background-color: rgba(#000000, 0.4);
		color: white;
	}
	&.active {
		background-color: rgba(#000000, 0.8);
		color: white;
	}
}
