footer{
    background-color: #0a1439;
    padding: 50px 0 0 0;
    .title{
        text-align: center;
        h1{
            font-size: 2.5rem;
            color: #ffffff;
            font-weight: 700;
            padding-bottom: 10px;
        }
        a{
            font-size: 2rem;
            color: #ffffff;
            font-weight: 700;
        }
    }
    .body-footer{
        margin-top: 30px;
        display: flex;
        justify-content: space-between;
        .left-body{
            .item{
                max-width: 350px;
                padding-bottom: 30px;
                h4{
                    color: #ffffff;
                    font-size: 16px;
                    text-transform: uppercase;
                    padding-bottom: 10px;
                }
                p{
                    color: #f3c12b;
                    font-size: 16px;
                }
                &:last-child{
                    padding-bottom: 0;
                }
            }
        }
        .right-body{
            text-align: center;
            max-width: 350px;
            h3{
                color: #ffffff;
                font-size: 20px;
                text-transform: uppercase;
                padding-bottom: 10px;
            }
            p{
                color: #fff;
                font-size: 16px;
                font-weight: 200;
                padding-bottom: 20px;
            }
            input[type="text"]{
                width: 75%;
                height: 35px;
                border: unset;
                border-radius: none;
                padding: 5px 10px;
                font-size: 16px;
                color:#0a1439;
                font-weight: 200;
            }
            input[type="submit"]{
                width: 23%;
                height: 35px;
                border: unset;
                border-radius: none;
                background-color: #f3c12b;
                color: #0a1439;
                font-size: 16px;
                text-transform: uppercase;
            }
        }
        @media(max-width: 480px){
            display: block;
            padding: 0 15px;
            .left-body{
                margin-bottom: 40px;
                .item{
                    max-width: unset;
                    width: 100%;
                }
            }
            .right-body{
                max-width: unset;
            }
        }
    }
    .copyright-bottom{
        width: 100%;
        background-color: #0A1439;
        text-align: center;
        padding: 5px 0 10px 0;
        margin-top: 40px;
        small{
            color:#f5c327;
            font-size: 12px; 
        }
    }
}